@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-itemMain {
  &:last-child {
    margin-bottom: 2rem;
  }
  &:nth-last-of-type(2) {
    img {
      border: 1px solid #d6d6d6 !important;
    }
  }
  .block-top {
    position: relative;

    &.color {
      background-color: black;
      .block-title {
        .title {
          @include title();
          color: white;
          //@include textGlitch("titleStrength", 17, white, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 600, 300);
        }
      }
    }
    .block-title {
      padding: 2rem 1rem 100px $pdMobile;
      padding-bottom: 100px;

      .title {
        @include title();
        font-size: 2rem;
        max-width: 70vw;
      }
    }
    img,
    video {
      position: absolute;
      background-color: black;
      max-width: calc(100vw - 4rem);
      left: $pdMobile;
      right: $pdMobile;
      margin: auto;
      bottom: -120px;
      border-radius: 20px;
      max-height: 210px;
    }
  }
  .wrapper-content {
    padding-top: 100px;
    .content {
      font-size: 1rem;
      padding-top: 50px;
      padding-left: $pdMobile;
      padding-right: $pdMobile;
    }
    .btn-item-offer {
      @include btn();
      @include btn-effect($hong-color);
      margin: $pdMobile;
    }
  }
}

@include tablet {
  .com-itemMain {
    .block-top {
      &.color {
        .block-title {
          .title {
            font-size: 3rem;
          }
        }
      }
      .block-title {
        padding: $pdMobile;
        padding-bottom: 5rem;
        max-width: 40vw;
        .title {
          font-size: 3rem;
        }
      }
      img,
      video {
        max-width: 50vw;
        max-height: 550px;
        width: calc(100vw - 4rem);
        left: $pdMobile;

        top: 50%;
        margin: unset;
        margin-left: 40vw;
      }
    }
    .wrapper-content {
      max-width: 40vw;
      padding-bottom: unset;
      padding-top: unset;
      .content {
        font-size: 1rem;
        padding: 2rem;
      }
      .btn-item-offer {
        display: block;
        margin: $pdMobile;
        margin-top: 3rem;
        @include btnDesk();
      }
    }
  }
}

@include desktop {
  .com-itemMain {
    @include desktop2k {
      height: 1500px;
    }
    .block-top {
      .block-title {
        padding: $pdDesk;
        padding-bottom: 7rem;
        max-width: 700px;
      }
      img,
      video {
        max-height: 60vh;
        max-width: 50vw;
        left: $pdDesk;
        margin-left: 35vw;
        object-fit: cover;
        @include desktop2k {
          max-height: unset;
          top: 40%;
        }
      }
    }
    .wrapper-content {
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
      display: flex;
      margin-left: 0vw;
      max-width: 40vw;
      height: 500px;
      padding: 2rem;
      margin-top: -50px;
      @include desktop2k {
        margin-top: unset;
      }
      .content {
        text-align: center;
        font-size: 18px;
        @include desktop2k {
          font-size: 1.25rem;
          max-width: 35vw;
        }
      }
      .btn-item-offer {
        margin: 0;
        max-width: unset;
        width: 330px;
        @include desktop2k {
          width: 500px;
        }
      }
    }
  }
}
