@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";

@include mobile {
  .com-value {
    padding-top: 30px;
    margin-bottom: 2.5rem;
    .block-item {
      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Montserrat", sans-serif;
        padding: 0 2rem;

        .wrapper-img {
          display: flex;
          justify-content: center;
          align-items: center;
          min-height: 200px;
          img {
            width: unset !important;
            max-width: 120px;
            max-height: 120px;
          }
          &.spec {
            img {
              max-width: 80px;
            }
          }
        }
        .title {
          text-align: center;
          font-size: 2rem;
          font-family: "Lato", sans-serif;
          font-weight: 800;
          margin-bottom: 1rem;
          &.spec {
            span {
              margin-right: 15px;
            }
          }
        }

        .content {
          text-align: center;
          color: $gray-color;
          font-weight: 500;
          font-style: italic;
          margin-bottom: 2rem;
        }
      }
    }
  }
}

@include desktop {
  .com-value {
    min-height: 500px;
    padding-top: 150px;
  }
}
.slick-slide {
  &:focus,
  a {
    outline: none;
  }
}
@include desktop2k {
  .com-value .block-item .item {
    .wrapper-img img,
    .wrapper-img.spec img {
      width: 280px;
      max-width: unset;
      max-height: unset;
    }
    .content {
      font-size: 1.5rem;
    }
  }
}
